.flag {
  display: inline-block;
  width: 300px;
  aspect-ratio: 14 / 9;
}
.flag.icon {
  width: 3em;
}
.flag.icon.large {
  width: 4em;
}
.flag.icon.small {
  width: 2em;
}
.flag.pink-triangle {
  background: conic-gradient(from 331deg at 50% 100%, #f7a 58deg, #0000 0) 50% 50% / 55% 73% no-repeat, black;
}
.flag.gilbert-baker {
  background: linear-gradient(
    hotpink 12.5%,
    red 0 25%,
    orange 0 37.5%,
    yellow 0 50%,
    green 0 62.5%,
    darkturquoise 0 75%,
    blue 0 87.5%,
    indigo 0
  );
}
