.flag.leather {
  position: relative;
  background: linear-gradient(#fff 0 0) 50% 50% / 100% 11.11% no-repeat,
    repeating-linear-gradient(black 0 11.11%, #282388 0 22.22%);
  &::after {
    content: "";
    top: 1.5%;
    left: 8.5%;
    position: absolute;
    width: 26.5%;
    height: 40%;
    background: radial-gradient(circle at 66.66% 33.33%, red 29.5%, #0000 30%),
      radial-gradient(circle at 33.33% 66.66%, red 29.5%, #0000 30%), linear-gradient(-45deg, red 50%, #0000 0);
    border-radius: 0 45%;
    transform-origin: bottom right;
    transform: rotate(1deg);
  }
}
.flag.rubber {
  --bg: linear-gradient(black 18.5%, red 0 23%, gold 0 37.75%, red 0 42%, black 0 57%, red 0 61.25%, black 0) no-repeat;
  position: relative;
  background: var(--bg), #000;
  overflow: hidden;
  background-size: 55% 100%;
  &::after {
    content: "";
    position: absolute;
    top: 28%;
    left: 30%;
    width: 100%;
    height: 100%;
    transform: rotate(-27deg);
    clip-path: polygon(57% 0, 100% 0, 100% 100%, 1% 100%);
    background: var(--bg);
    background-size: 100% 100%;
  }
  &::before {
    content: "";
    position: absolute;
    top: 18%;
    left: 0%;
    width: 100%;
    height: 100%;
    transform: rotate(55.5deg);
    clip-path: polygon(19% 0, 100% 0, 100% 100%, 53% 100%);
    background: var(--bg);
    background-size: 100% 100%;
  }
}
